.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.ol-overlaycontainer-stopevent:before {
  content: "";
  background-image: 
    radial-gradient(circle, transparent 20%, rgba(0,0,0,.8) 80%),
    radial-gradient(circle at 33% 33%, rgba(255,255,255,.3), transparent 20%);
  pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}

.map {
  background-color: rgba(0, 0, 0, 0.5);
}

.ol-vector-layer {
  z-index: 900 !important;
}

.osm-basemap {
  z-index: 899 !important;
}

/* WebKit browsers (Chrome, Safari, Edge) */
::-webkit-scrollbar {
  width: 8px; /* Scrollbar genişliği */
  border-radius: 10px; /* Uçlarını yuvarlak yap */
}

::-webkit-scrollbar-track {
  background: #f1f1f1; /* Scrollbar arka plan rengi */
  border-radius: 10px; /* Track köşeleri */
}

::-webkit-scrollbar-thumb {
  background: #888; /* Scrollbar tutamacının rengi */
  border-radius: 10px; /* Tutamacın köşeleri */
}

::-webkit-scrollbar-thumb:hover {
  background: #555; /* Hover üzerindeyken scrollbar tutamacının rengi */
}

/* Firefox için */
* {
  scrollbar-width: thin; /* İnce scrollbar */
  scrollbar-color: #888 #f1f1f1; /* Renkler */
}

